.blue-underline-btn {
  position: relative;
  cursor: pointer;
  font-style: italic;
  font-size: 20px;
  font-weight: 300;
  color: #555;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.blue-underline-btn:hover {
  letter-spacing: 0.5px;
}

.blue-underline-btn:after {
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  content: '';
  background-color: #037ef3;
  height: 2px;
  width: 100%;
  margin: auto;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

